.te-theme .postLoader {
  position: relative;
  height: 100%;
  min-height: 100vh;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  max-width: 72px;
  margin: 0 auto;
  background-color: transparent;
}
.te-theme .postLoader svg {
  width: 180px;
  height: 32px;
}
.te-theme .loading {
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  z-index: 99;
  background: rgba(255, 255, 255, 0.9);
}
.te-theme .postLoader > div {
  width: 30px;
  height: 30px;
  border: 2px dotted transparent;
  border-radius: 50%;
  padding: 10px;
  box-shadow: inset 0 1px 14px rgba(0, 0, 0, 0.3);
  box-sizing: unset;
}
.te-theme .postLoader > div {
  -webkit-animation: 1s cubic-bezier(0.455, 0.03, 0.515, 0.955) infinite
    alternate flip-vertical-right;
  animation: 1s cubic-bezier(0.455, 0.03, 0.515, 0.955) infinite alternate
    flip-vertical-right;
}
@-webkit-keyframes flip-vertical-right {
  0% {
    -webkit-transform: rotateY(0) scale(0.9);
    transform: rotateY(0) scale(0.9);
  }
  100% {
    -webkit-transform: rotateY(360deg) scale(1);
    transform: rotateY(360deg) scale(1);
  }
}
@keyframes flip-vertical-right {
  0% {
    -webkit-transform: rotateY(0) scale(0.9);
    transform: rotateY(0) scale(0.9);
  }
  100% {
    -webkit-transform: rotateY(360deg) scale(1);
    transform: rotateY(360deg) scale(1);
  }
}
