.cldtd_calendar_card_sidebar_day {
  width: 55vw;
  height: 100%;
  line-height: 1.5;
  background: #353236;
}
.cldtd_calendar_card_main_day {
  width: 45vw;
  display: flex;
  padding: 20px;
  justify-content: center;
  align-content: center;
  text-align: center;
  flex-direction: column;
  color: #353236;

}
.cldtd_calendar_card_DateDay {
  font-size: 3vw;
}
.cldtd_calendar_card_YearMonth {
  font-size: 1.5vw;
}
.cldtd_calendar_card_sidebar_event_date_Day {
  font-size: 0.9vw;
  width: 25%;
  font-weight: 600;
}
.fc .fc-list-empty-cushion {
  margin: 5em 0;
  color: #ccc;
}
.cldtd_calendar_card_sidebar .fc .fc-scroller-liquid {
  height: auto;
}
.cldtd_calendar_card_sidebar_day .fc-list-event-graphic {
  padding-left: 0 !important;
  padding-right: 0 !important;
}