.intro_slide_content {
    max-width: 500px;
}

.intro_slides .carousel-indicators li {
    box-sizing: content-box;
    flex: 0 1 auto;
    width: 8px;
    height: 8px;
    border-radius: 50%;
    margin-right: 5px;
    margin-left: 5px;
    text-indent: -999px;
    background-color: #545454;
    background-clip: padding-box;
    border-top: 10px solid transparent;
    border-bottom: 10px solid transparent;
    opacity: .5;
    transition: opacity 0.6s ease;
}
.carousel-control-prev, .carousel-control-next {
    width: 13%;
}
.intro_slides .carousel-indicators {
    position: absolute;
    right: 0;
    bottom: -30px;
    left: 0;
    z-index: 15;
    display: flex;
    justify-content: center;
    padding-left: 0;
    margin-right: 15%;
    margin-left: 15%;
    list-style: none;
}

.intro_slides .carousel-indicators .active {
    opacity: 1;
    background-color: #FF4051;
}

.intro_modal .modal-content {
    border-radius: 0;
    background-color: transparent;
}

#vim_video_player {
    width: 100%;
    height: auto;
}
.intro_modal .intro_close {
    left: auto;
    right: 0px;
    position: absolute;
    top: -50px;
    border-radius: 50%;
    background-color: rgba(255, 255, 255, .1);
    height: 36px;
    width: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s ease-in-out;
}
.intro_modal .intro_close:hover{
    background-color: rgba(255, 255, 255, .15);
}


@media (min-width: 576px){
.intro_modal .modal-content {
    box-shadow: none;
}
.intro_modal .intro_close {
    right: -60px;
}
}
.intro_slides .carousel-control-prev-icon, .intro_slides .carousel-control-next-icon {
    display: inline-block;
    width: 30px;
    height: 30px;
    background: no-repeat 50% / 100% 100%;
    border-radius: 50%;
    background-position: center;
    opacity: 1;
}

.intro_slides .carousel-control-prev:hover, .intro_slides .carousel-control-prev:focus, .intro_slides .carousel-control-next:hover, .intro_slides .carousel-control-next:focus {
    opacity: 1;
}

.intro_slides .carousel-control-prev, .intro_slides .carousel-control-next{
    opacity: 1;
}

.intro_slides .carousel-control-next-icon {
    background-image: url(data:image/png;base64,);
}

.intro_slides .carousel-control-prev-icon {
    background-image: url(data:image/png;base64,);
}

.intro_slides .carousel-inner .active {
  animation: none!important;
}

.intro_video{
