.cldtd_calendar_card_Week {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.cldtd_calendar_card_sidebar_Week {
  width: 100%;
  background: #353236;
}
.cldtd_calendar_card_main_Week {
  width: 100%;
}
.cldtd_calendar_card_main_Week .fc .fc-scrollgrid-liquid {
  height: 25%;
}
.cldtd_calendar_card_main_Week .fc .fc-toolbar.fc-header-toolbar {
  background: #353236;
  margin: 0;
}
.cldtd_calendar_card_main_Week .fc .fc-toolbar-title {
  font-size: 1.7rem;
  padding: 3px;
}
.cldtd_calendar_card_main_Week .fc table {
  font-size: 1rem;
}
.cldtd_calendar_card_main_Week .fc-toolbar-title {
  color: #fff;
  font-weight: 400;
}
.cldtd_calendar_card_main_Week .fc .fc-col-header-cell-cushion {
  font-weight: 500;
}
/* .cldtd_calendar_card_main_Week .fc .fc-bg-event {
  opacity: 1;
} */
 .cldtd_calendar_card_main_Week .fc-event-title {
  text-overflow: ellipsis;
  text-transform: capitalize;
} 
.cldtd_calendar_card_main_Week .fc .fc-daygrid-body-natural .fc-daygrid-day-events {
  margin-bottom: 0;
}
.cldtd_calendar_card_sidebar_Week .fc .fc-view-harness {
  height: 100vh !important;
}
.cldtd_calendar_card_main_Week .fc-scroller.fc-scroller-liquid-absolute {
  overflow: hidden !important;
}
.cldtd_calendar_card_main_Week .fc-scroller-harness .fc-scroller {
  overflow: hidden !important;
} 
 .cldtd_calendar_card_main_Week .fc .fc-view-harness-active > .fc-view {
  position: relative !important;
}
.cldtd_calendar_card_main_Week .fc .fc-view-harness {
  height: auto !important;
}
.cldtd_calendar_card_main_Week .fc-scrollgrid-section.fc-scrollgrid-section-body.fc-scrollgrid-section-liquid {
  height: 50px !important;
}
.cldtd_calendar_card_main_Week .fc .fc-col-header-cell-cushion {
  display: inline-block;
  padding: 11px 4px;
}
.cldtd_calendar_card_sidebar_date_week {
  font-size: 1.4rem;
  padding: 0.4em 0.7em;
  color: #D5D5D5;
  font-weight: 500;
  border-bottom: 1px solid #56505c;
}
.cldtd_calendar_card_sidebar_Week .fc-direction-ltr .fc-list-day-side-text, .fc-direction-rtl .fc-list-day-text {
  display: block !important;
  color: #fff;
}

.cldtd_calendar_card_sidebar_Week .fc-theme-standard td, .cldtd_calendar_card_sidebar_Week .fc-theme-standard th{
  border:none
}
.cldtd_calendar_card_sidebar_Week tr.fc-list-event.fc-event.fc-event-start.fc-event-end.fc-event-future {
  display: inline-table;
  width: 33.33%;
  border-right: 1px solid;
}
.cldtd_calendar_card_sidebar_Week .fc .fc-list-empty {
  height: auto;
}
.cldtd_calendar_card_sidebar_Week .fc-list-event-graphic {
  padding-left: 0 !important;
  padding-right: 0 !important;
}
.fc .fc-list-event-title a {
  text-transform: capitalize;
}
.fc-list-event-time {
  text-transform: capitalize;
}
.fc .fc-list-event-title a {
  text-transform: capitalize;
  height: 20px !important;
  display: block;
  overflow: hidden;
  text-overflow: ellipsis;
}