.calendly-inline-widget{
    height: calc(100vh - 70px);
}
.ugc-calendly-form .btn-close{
    position: absolute;
    right: 15px;
    z-index: 1;
    top: 15px;
}
.ugc-calendly-form::before{
    content: '';
    background-image: url(../../../../public/assets/images/ugc-bg-new.png);
    background-size: cover;
    width: 100%;
    height: 100%;
    left: 0px;
    top: 0px;
    position: absolute;
    filter: blur(2px);
}
.ugc-calendly-form .walls_tabs{
    z-index: 1;
}
.ugc-calendly-form::after{
    content: '';
    background-color: #0000001a;
    position: absolute;
    top: 0px;
    width: 100%;
    left: 0px;
    display: block;
    height: 100%;
}
.ugcform-height{
    display: flex;
    align-items: center;
    width: 100%;
}
.ugcform-height #my-hubspot-form{
    position: relative;
    width: 700px;
    margin: 0 auto;
    overflow: auto;
    border-radius: 10px;
    background-color: #fff;
    padding: 15px 30px;
}
@media (max-width: 1600px){
    .ugcform-height .my-hubspot-form-main{
        transform: scale(0.9);
    }
}
@media (max-width: 1366px) {
    .ugcform-height #my-hubspot-form{
        max-height: auto;
        overflow: hidden;
        width: 100%;
    }
    .ugcform-height .position-relative{
        width: 50%;
    }
    
    .ugcform-height #my-hubspot-form{
        width: 100%;
        padding: 15px;
    }

}

@media (max-width: 992px) {
    .ugcform-height .position-relative{
        width: 80%;
    }
    .ugcform-height{
        align-items: start;
        margin-top: 30px;
    }
}
@media (max-width: 767px) {
    .ugcform-height #my-hubspot-form{
        transform: inherit;
    }
    .ugc-calendly-form .btn-close {
        right: 30px;
        top: 55px;
    }
    .ugcform-height .my-hubspot-form-main{
        margin-top: 40px;
    }
}
@media (max-width: 576px) {
    .ugcform-height .position-relative{
        width: 95%;
    }
}