.nbtd_view_wall {
  width: 80%;
  margin: auto;
}
.nbtd_wall_wraper {
  position: relative;
  display: block;
  content: "";
  width: 100%;
  padding-top: 56.25%;
}
.nbtd_noticboard_card {
  border: 6px solid #2b2b2b;
  background-repeat: no-repeat;
  background-size: cover;
  padding: 2%;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}
.nbtd_logo_icon {
  display: flex;
}
.nbtd_logo_icon_img {
    /* max-width: 50px; */
  max-height: 50px!important;
}
.nb_wrap_00 {
  padding: 3%;
  height: 100%;
  width: 100%;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
}

.nb_wrap_in_001{
  height: 100%;
  width: 100%;
  position: relative;
  overflow: hidden;
}

.notic_board .ql-container {
  max-height: 250px;
  overflow-y: auto;
}

.nbtb_quill_custom_design .ql-toolbar.ql-snow .ql-formats {
  border: 1px solid rgb(204, 204, 204);
  border-radius: 4px;
 
}
.nbtb_quill_custom_design .ql-snow .ql-picker {
  color: rgb(51, 51, 51);
  font-weight: 400;
}
.nbtb_quill_custom_design .ql-size-large,
.nbtb_quill_custom_design .ql-size span[data-value="Extra-large"]::before {
  font-size: 50px !important;
}
.hide {
  display: none;
}
.css-199jh5z-ColorPicker{
  /* display: none!important; */
}
.uppy_upload_btn {
  padding: 20px 15px;
  border: 2px dashed #aaa;
  border-radius: 3px;
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  cursor: pointer;
}
.ql-editor{
  min-height:200px;
}
.notice_board_color span[type="color"] {
  width: 100%;
  height: 34px;
}

.notic_board .uppy .uppy-thumbnails .uppy-thumbnail-container {
  width: 100%;
}

.notic_board .uppy .uppy-thumbnails .uppy-thumbnail-container .uppy-thumbnail-label {
  white-space: nowrap;
  max-width: 55%;
  overflow: hidden;
  text-overflow: ellipsis;
}