.max-height {
  height: 100vh;
}
.max-width {
  width: 100vw;
}
.left-bar {
  background-color: #1a2029;
  overflow: scroll;
}
.central-panel {
  background-color: #f2f6f8;
}
.add-btn{
      background: #FC4051 0% 0% no-repeat padding-box;
    border-radius: 3px;
    opacity: 1;
    padding: 3px;
    font-size: 14px;
}
.side-bar-scene-timer{
  font-size: 12px;
    color: #9A9DA1;
}
.side-bar-scene-text{
  font-size: 16px;
    color: #E9EEF0;
}
.side-bar-scene{
  margin: 0px;
  border: 1px solid var(--unnamed-color-ffffff);
  background: #353B43 0% 0% no-repeat padding-box;
  border: 1px solid #FFFFFF;
  border-radius: 4px;
  opacity: 1;
  padding: 4px;
  border: none!important;
}
