.tb_rv_banner_wrap {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 15px 20px;
}

.tb_rv_head_text,
.tb_rv_rating_wrap {
    display: flex;
    align-items: center;
}

.tb_rv_head_text .tb_rv_head_social_text {
    margin-right: 5px;
}

.bannerPreviewCard {
    border-radius: 4px;
}

.tb_rv_banner_btn_wrap .commanBtn {
    border-color: transparent !important;
}

.tb_rv_review_count {
    font-size: 18px;
    font-weight: 600;
    margin-right: 10px;
}

.tb_rv_rview_star {
    margin-right: 10px;
}

.tb_rv_rating_wrap .tb_rv_review_count {
    display:flex;
}
@media(max-width:474px) {
    .tb_rv_banner_wrap {
        flex-direction: column;
        align-items: flex-start;
    }
    .tb_rv_banner_btn_wrap {
        margin-top: 7px;
    }
}
