@font-face {
  font-family: digital-7;
  font-style: normal;
  font-weight: 400;
  src: local("Digital-7"), local("Digital-7-Regular"),
    url("./font/digital-7_b73ef91485431ac0271f24009e14ccee.ttf") format("truetype"),
    url("./font/digital-7_b73ef91485431ac0271f24009e14ccee.woff") format("woff");
}
.cdtd_clock_card {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  /* background: rgb(7, 74, 123);
  background: linear-gradient(153deg, rgba(7, 74, 123, 1) 0%, rgb(4 47 78) 50%, rgba(58, 14, 55, 1) 100%); */
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  overflow: hidden;
  bottom: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.cdtd_clock_overlay {
  background: #0000005c;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
  position: absolute;
}
.cdtd_clock_overlay_none {
  display: none;
}
.cdtd_clock_cardContent {
  padding: 5%;
  max-width: 100%;
  margin: auto;
  position: relative;
  border-radius: 15px;
  color: #fff;
  width: 100%;
}

.cdtd_clock_date {
  font-size: 3.6vw;
  text-align: center;
  font-weight: 500;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 2%;
}
.cdtd_clock_time {
  font-size: 6vw;
  text-align: center;
  font-weight: bold;
  display: flex;
  align-items: baseline;
  justify-content: center;
}
.cdtd_clock_timeType {
  font-size: 2vw;
  font-weight: 400;
}
.cdtd_clock_list {
  display: flex;
  justify-content: center;
}
.cdtd_clock_day {
  padding: 1% 15px;
  font-size: 1.2vw;
  color: #fff;
  text-align: center;
  opacity: 0.4;
  text-transform: capitalize;
  font-weight: 400;
}
.cdtd_clock_actice_date {
  opacity: 1;
}
.cdtd_clock_color span[type="color"] {
  width: 100%;
  height: 34px;
}

.cdtd_clock .uppy .uppy-thumbnails .uppy-thumbnail-container {
  width: 100%;
  margin-top: 0;
}

.cdtd_clock .uppy .uppy-thumbnails .uppy-thumbnail-container .uppy-thumbnail-label {
  white-space: nowrap;
  max-width: 55%;
  overflow: hidden;
  text-overflow: ellipsis;
}

.tbdt_switchButton {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.cdtd_clock_cardContent_type_Classic .cdtd_clock_time {
  font-family: "digital-7";
  text-shadow: 0px 3px 6px #00000029;
  font-size: 8vw;
  letter-spacing: 4px;
}
.cdtd_clock_Classic_DayDate {
  display: flex;
  justify-content: center;
  align-items: center;
}
.cdtd_clock_Classic_DayDate-code {
  display: flex;
  justify-content: center;
  align-items: center;
}
.clk_display_none{
  display:none;
}
.cdtd_clock_Classic_Day {
  font-size: 5vw;
  text-transform: uppercase;
}
.cdtd_clock_Classic_Date {
  text-align: center;
  margin-left: 10px;
}
.cdtd_clock_Classic_DateMonth {
  font-size: 1.8vw;
  text-transform: uppercase;
}
.cdtd_clock_Classic_DateYear {
  font-size: 1.8vw;
}
@media only screen and (max-width: 678px) {
  .cdtd_clock_day {
    font-size: 12px;
    padding: 1% 5px;
  }
  .cdtd_clock_Classic_Day {
    font-size: 22px;
  }
  .cdtd_clock_Classic_DateMonth {
    font-size: 11px;
  }
  .cdtd_clock_Classic_DateYear {
    font-size: 11px;
  }
  .cdtd_clock_timeType {
    font-size: 10px;
  }
}


